<template>
  <section class="w-100 p-4 d-flex flex-column justify-content-between" style="height: 100vh">
    <section>
      <div class="mb-5">
        <div class="d-flex align-items-center justify-content-between">
          <h2>商品列表</h2>
          <button
            type="button"
            class="btn btn-outline-dark btn-lg rounded-3"
            @click="openModal(true)"
          >
            <i class="fa-solid fa-plus me-2"></i>新增商品
          </button>
          <label for="inputState" class="form-label"
            ><p class="d-none">State</p>
            <select id="inputState" class="form-select rounded-3 p-2 px-3">
              <option selected>新->舊...</option>
              <option>按類別</option>
            </select>
          </label>
        </div>
      </div>
      <div>
        <table class="table table-striped table-hover align-middle">
          <thead>
            <tr>
              <th scope="col">類別</th>
              <th scope="col">標題</th>
              <th scope="col">價格</th>
              <th scope="col">數量</th>
              <th scope="col">圖片</th>
              <th scope="col">細節</th>
              <th scope="col">啟用</th>
              <th scope="col">刪除</th>
              <th scope="col">修改</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="item in products" :key="item.id">
              <th scope="row">{{ item.category }}</th>
              <td>{{ item.title }}</td>
              <td>{{ item.price }}</td>
              <td>{{ item.num }}</td>
              <td><img :src="item.imageUrl" alt="" style="width: 150px" /></td>
              <td>
                <button type="button" class="btn btn-outline-info btn-sm">
                  <i class="fa-solid fa-eye"></i>
                </button>
              </td>
              <td v-if="item.is_enabled"><i class="far fa-check-square text-success h3"></i></td>
              <td v-else><i class="far fa-times-circle h3 text-warning"></i></td>
              <td>
                <button
                  type="button"
                  class="btn btn-outline-danger btn-sm"
                  @click="checkDelete(item)"
                >
                  <i class="fas fa-trash-alt"></i>
                </button>
              </td>
              <td>
                <button
                  type="button"
                  class="btn btn-outline-lulu2 btn-sm"
                  @click="openModal(false, item)"
                >
                  <i class="fas fa-pen"></i>
                </button>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
      <!-- 新增修改模組 -->
      <AddEdit ref="addedit" :isNew="isNew" :product="product" @render="getProducts"></AddEdit>
      <!-- 刪除確認模組 -->
      <CheckDelete
        :product="product"
        @delete="deleteProduct"
        :loading="loadingStatus"
        ref="check"
      ></CheckDelete>
    </section>
    <PaginationModal
      class="d-flex justify-content-center"
      :page="pagination"
      @get_product="getProducts"
    />
  </section>
</template>

<script>
import AddEdit from './addEditDelete/AddEditProduct.vue';
import CheckDelete from './addEditDelete/CheckDelete.vue';
import PaginationModal from './PaginationModal.vue';

export default {
  data() {
    return {
      loadingStatus: '',
      products: [],
      product: {},
      pagination: {},
      isNew: false,
    };
  },
  components: {
    AddEdit,
    CheckDelete,
    PaginationModal,
  },
  methods: {
    // 取得資料
    getProducts(page = 1) {
      const url = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/admin/products?page=${page}`;
      this.$http.get(url).then((res) => {
        if (res.data.success) {
          this.products = res.data.products;
          this.pagination = res.data.pagination;
        }
      });
    },
    // open add&edit Modal
    openModal(isNew, item) {
      this.isNew = isNew;
      if (this.isNew) {
        this.product = {
          imagesUrl: [],
        };
      } else {
        this.product = { ...item };
      }
      this.$refs.addedit.openModal();
    },
    // 取得單筆文章並起開啟修改modal
    // getProduct(id) {
    // const url = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/admin/product/${id}`;
    //   this.$http.get(url).then((res) => {
    //     this.product = res.data.product;
    //     this.openModal(false, this.product);
    //   }).catch((error) => {
    //     console.log(error);
    //   });
    // },
    // check刪除
    checkDelete(item) {
      this.product = item;
      this.$refs.check.openModal();
    },
    // 刪除文章
    deleteProduct(id) {
      this.loadingStatus = id;
      const url = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/admin/product/${id}`;
      this.$http.delete(url).then((res) => {
        if (res.data.success) {
          this.getProducts();
          this.loadingStatus = '';
          this.$refs.check.hideModal();
        }
      });
    },
  },
  mounted() {
    this.getProducts();
  },
};
</script>
